
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


import {
jsonpReturn, //处理返回的数据
checkRequest
}
from '@/libs/util'

import {
mapGetters
} from 'vuex'

import {
ChangePassword, //发送短信验证码
}
from '@/api/user'


export default {
data() {
return {
form: {
old_password: '',
password: '',
confirm_password: ''
},
showAddress: false,
toastText: '', // 弹出框
toast: false
}
},
components: {
Group,
XInput,
XButton,
Divider,
Flexbox,
FlexboxItem,
Toast,
Cell,
},
computed: {
...mapGetters([
'username',
'telphone',
'token',
])
},
methods: {
submit() {
let check = this.$refs.old_password.valid && this.$refs.password.valid
if (check && this.form.password == this.form.confirm_password) {
ChangePassword(
this.telphone,
this.form.old_password,
this.form.password,
this.form.confirm_password,
).then((res) => {
if (checkRequest(res)) {
this.toast = true
this.toastText = checkRequest(res)
setTimeout(() => {
this.$router.push('center')
}, 1000)
} else {
this.toast = true
this.toastText = '原密码填写有误'
}
})
} else {
this.toast = true
this.toastText = '请确保表单信息正确'
}
}
}
}
